<template>
  <wrapper titulo="Búsqueda de órdenes">
    <v-tabs v-if="!alumnoEncontrado" centered fixed-tabs>
      <v-tab>
        Por nombre ó matrícula
      </v-tab>
      <v-tab>
        Por número de órden
      </v-tab>

      <v-tab-item class="text-center ma-7">
        <v-form v-model="formNombre" ref="formNombre" lazy-validation
                v-click-outside="resetFormNombre">
          <v-row>
            <v-col cols="12" class="mt-3">
              <h3 class="text--disabled text-center">Buscar por nombre</h3>
            </v-col>
            <v-col cols="12" md="4">
              <v-text-field v-model="nombre" label="Nombre" :rules="nameRules" autofocus/>
            </v-col>
            <v-col cols="12" md="4">
              <v-text-field v-model="apellidoPaterno" label="Apellido Paterno" :rules="nameRules"/>
            </v-col>
            <v-col cols="12" md="4">
              <v-text-field v-model="apellidoMaterno" label="Apellido Materno" :rules="nameRules"/>
            </v-col>
            <v-col cols="12" class="text-center">
              <v-btn :disabled="!nombre || !apellidoPaterno || !apellidoMaterno" rounded color="primary"
                     @click="buscarAlumnoPorNombre">Buscar</v-btn>
            </v-col>
          </v-row>
        </v-form>

        <v-row v-click-outside="resetFormMatricula">
          <v-col cols="12" class="mt-5">
            <h3 class="text--disabled text-center">Buscar por matrícula</h3>
            <v-text-field v-model="matricula" ref="matricula" label="Matrícula" :rules="nameRules" @keyup.enter="buscarAlumnoPorMatricula()"/>
          </v-col>
          <v-col cols="12" class="text-center">
            <v-btn rounded color="primary" :disabled="!matricula" @click="buscarAlumnoPorMatricula">Buscar</v-btn>
          </v-col>
        </v-row>
      </v-tab-item>

      <v-tab-item class="text-center ma-9">
        <v-row>
          <v-col cols="12" class="mt-3">
            <v-text-field v-model="numOrden" label="Número de pedido" :rules="nameRules" autofocus/>
          </v-col>
          <v-col cols="12" class="text-center">
            <v-btn :disabled="!numOrden" rounded color="primary"
                   @click="buscarAlumnoPorNombre">Buscar</v-btn>
          </v-col>
        </v-row>
      </v-tab-item>
    </v-tabs>

    <template v-else>
      <v-row>
        <v-col cols="12">
          <v-card class="pa-2 text-capitalize">
            <v-row>
              <v-col cols="12" md="4">
                <v-img v-if="alumnoEncontrado.refImagen" :src="alumnoEncontrado.refImagen" width="150"/>
                <v-img v-else src="../../../assets/nino.jpg" width="150"/>
              </v-col>
              <v-col cols="12" md="6">
                <v-card-text class="text--disabled">
                  <h2>{{ alumnoEncontrado.nombreCompleto }}</h2>
                  <h2>{{ alumnoEncontrado.matricula }}</h2>
                  <h2>{{ alumnoEncontrado.grado }} {{ alumnoEncontrado.grupo }}</h2>
                  <h2 class="success--text">{{ alumnoEncontrado.cafeteria.saldoAlumno | moneda }}</h2>
                </v-card-text>
              </v-col>
            </v-row>
            <v-card-text>
              <v-col cols="12" md="4">
                <v-select v-model="cafeteriaSeleccionada" label="Cafetería" :items="cafeterias"
                          item-text="nombre" item-value="id" @change="filtrarCafeterias"/>
              </v-col>
              <v-data-table :headers="headers" :items="ordenesFiltradas">
                    <template v-slot:item.actions="{ item }">
                      <v-btn rounded color="primary" x-small outlined class="mx-3" @click="verProductos(item)">Productos</v-btn>
                      <v-btn rounded color="primary" x-small outlined class="mx-3" @click="dialogAgregarProducto = true">Agregar productos</v-btn>
                      <v-btn rounded color="error" x-small outlined class="mx-3">Cancelar</v-btn>
                    </template>
                <template v-slot:item.estatus="{ item }">
                  <v-chip v-if="item.estatus.titulo = 'ENTREGADA'" x-small color="success">{{ item.estatus.titulo }}</v-chip>
                  <v-chip v-else-if="item.estatus.titulo = 'CANCELADA'" x-small color="error">{{ item.estatus.titulo }}</v-chip>
                  <v-chip v-else-if="item.estatus.titulo = 'CREADA'" x-small color="primary">{{ item.estatus.titulo }}</v-chip>
                </template>
                  </v-data-table>
            </v-card-text>
          </v-card>
        </v-col>
        <v-col cols="12">
          <v-btn outlined rounded color="primary" @click="nuevaBusqueda">Nueva búsqueda</v-btn>
        </v-col>
      </v-row>
    </template>

    <dialogo v-model="dialogProductosEnOrden" title="Productos en la órden">
      <v-row v-if="ordenes && ordenes.length > 0">
        <v-data-table :headers="headersProductos" :items="ordenParaVisualizar.productos">

        </v-data-table>

        <v-col v-if="ordenParaVisualizar.id" cols="12" class="text-center">
          <h2 v-if="ordenParaVisualizar.estatus.titulo === 'ENTREGADA'">Órden entregada</h2>
          <v-btn v-else-if="ordenParaVisualizar.estatus.titulo === 'CREADA'" rounded color="success" @click="dialogoDeseaEntregarOrden = true">Entregar órden</v-btn>
        </v-col>
      </v-row>
    </dialogo>

    <dialogo v-model="dialogAgregarProducto" title="Agregar Productos a la orden" ancho="700">
      <v-row v-if="ordenParaActualizar.alumno">
        <v-col cols="12">
          <h4 class="caption">Seleccione los productos que desea agregar a su orden</h4>
        </v-col>
        <v-col cols="12">
          <carrusel>
            <carrusel-item v-for="(categoria, indexCategorias) in categorias" :key="indexCategorias" size="41%">
              <categoria :categoria="categoria" :esta-seleccionada="categoriaSeleccionada && categoria.id === categoriaSeleccionada" @seleccionarCategoria="seleccionarCategoria(categoria)"/>
            </carrusel-item>
          </carrusel>
        </v-col>
        <v-col v-for="(producto, indexProducto) in productosFiltrados" :key="indexProducto" cols="12" md="6">
          <producto :producto="producto" opcion-agregar mostrar-acciones
                    :producto-agregado="existeProductoEnOrdenParaActualizar(producto)" @seleccionar="agregarProductoOrden(producto)"
                    :productos-bloqueados-alumno="ordenParaActualizar.alumno.productosBloqueados" />
        </v-col>
      </v-row>
    </dialogo>

    <dialogo v-model="dialogoDeseaEntregarOrden" title="¿Desea entregar orden?">
      <template slot="actions">
        <v-btn text color="primary" plain @click="entregarOrden">Aceptar</v-btn>
      </template>
    </dialogo>

  </wrapper>
</template>

<script>

import OrdenesService from "../../../services/OrdenesService";
import Carrusel from "../../General/Carrusel/Carrusel";
import CarruselItem from "../../General/Carrusel/CarruselItem";
import Categoria from "../../Tienda/Categoria";
import Producto from "../../Tienda/Producto";
import CategoriasService from "../../../services/CategoriasService";
import ProductosService from "../../../services/ProductosService";
import Cafeterias from "@/components/UsuarioAdministrador/Cafeterias";
import CafeteriasService from "@/services/CafeteriasService";

export default {
  name: "PanelDeBusquedaDeOrdenes",
  components: {Producto, Categoria, CarruselItem, Carrusel},
  data(){
    return{
      nombre: null,
      apellidoPaterno: null,
      apellidoMaterno: null,
      matricula: null,
      nameRules: [v => !!v || 'Este campo es requerido'],
      formNombre: false,
      ordenes: [],
      alumnoEncontrado: null,
      headers: [
        { text: 'Cafetería', value: 'cafeteria.nombre' },
        { text: '# de órden', value: 'id' },
        { text: 'Fecha', value: 'fecha' },
        { text: 'Estatus', value: 'estatus' },
        { text: '', value: 'actions', sortable: false },
      ],
      headersProductos: [
        { text: 'Producto', value: 'producto.nombre' },
        { text: 'Cantidad', value: 'cantidad' },
        { text: 'Precio', value: 'producto.costo' },
        { text: '', value: 'actions', sortable: false },
      ],
      productosEnOrden: [],
      dialogProductosEnOrden: false,
      dialogAgregarProducto: false,
      ordenParaActualizar: {},
      categorias: [],
      productos: [],
      productosFiltrados:[],
      dialogoDeseaEntregarOrden: false,
      ordenParaVisualizar: {},
      cafeterias: [],
      cafeteriaSeleccionada: null,
      ordenesFiltradas: []
    }
  },

  async created() {
    try {
      this.categorias = await CategoriasService.getCategorias();
    } catch (e) {
      this.categorias = []
      console.log(e)
    }
    try {
      this.productos = await ProductosService.getProductos();
    } catch (e) {
      this.productos = []
      console.log(e)
    }

    try {
      this.cafeterias = await CafeteriasService.getCafeterias(this.$usuario.cliente.id)
    } catch (e) {
      this.cafeterias = []
      console.log(e)
    }

    if(!this.categoriaSeleccionada){
      this.productosFiltrados = this.productos
    }
  },

  methods: {
    resetFormNombre() {
      this.$refs.formNombre.resetValidation();
    },
    resetFormMatricula() {
      this.$refs.matricula.resetValidation();
    },
    async buscarAlumnoPorMatricula() {
      this.$loader = true
      try {
        this.ordenes = await OrdenesService.getPorMatriculaAlumno(this.matricula)
        this.alumnoEncontrado = this.ordenes.length ? this.ordenes[0].alumno : {}

      } catch (e) {
        this.alumnoEncontrado = {}
        console.log(e)
      }
      this.$loader = false

    },
    verProductos(orden) {

      this.ordenParaVisualizar = orden
      this.dialogProductosEnOrden = true
    },
    nuevaBusqueda() {
      this.ordenes = []
      this.alumnoEncontrado = null
    },

    async entregarOrden() {
      this.$loader = true
      try {
        let ordenEntregada = await OrdenesService.entregarOrden(this.ordenParaVisualizar.id)
        this.$showAlerta('600', 'success', 'mdi-check-circle-outline', 'Ordén entregada exitosamente', true);
      } catch (e) {
        this.$showAlerta('600', 'warning', 'mdi-alert-circle',
          'No fue posible entregar la órden', false);
      }
      this.$loader = false
      this.dialogoDeseaEntregarOrden = false
      this.dialogProductosEnOrden = false
    },

    filtrarCafeterias () {
      let vm = this;
      if (vm.cafeteriaSeleccionada) {
        vm.ordenesFiltradas = vm.ordenes.filter(function (ordenFiltrada) {
          let idCafeteria = ordenFiltrada.idCafeteria;
          return (idCafeteria == vm.cafeteriaSeleccionada)
        })
      } else {
        vm.ordenesFiltradas = vm.ordenes
      }
    }
  }
}
</script>

<style scoped>

</style>
